import { useState } from 'react';
import './App.css';
import copy from "copy-to-clipboard";

function App() {
  const [key, setInput1] = useState('');
  const [data, setInput2] = useState('');
  const [message, setMessage] = useState('');
  const [isCopied, setIsCopied] = useState(false);
  const handleChange = (event) => {
    const { name, value } = event.target;
    if (name === 'key') {
      setInput1(value);
    } else if (name === 'data') {
      setInput2(value);
    }
  };

  function generateSignature(token, data) {
    const crypto = require("crypto");
    const hmac = crypto.createHmac("sha256", token);
    hmac.update(data);
    const signature = hmac.digest('hex');
    return signature;
  }

  const handleClick = () => {
    // Perform an action based on the input values
    setMessage(generateSignature(key, data));
    setIsCopied(false)

  };

const copyToClipboard = () => {
    copy(message);
    setIsCopied(true)
};

  return (

    <div id='container'>
      <form id="input-form">
        <header id="label">Signature generator</header>
        <input
          type="text"
          name="key"
          id='input'
          placeholder="Enter your secret key"
          value={key}
          onChange={handleChange}
        />
        <input
          type="text"
          name="data"
          id='input'
          placeholder="Enter your path"
          value={data}
          onChange={handleChange}
        />
        <button type='button' onClick={handleClick}>Generate Signature</button>
      </form>

      <div >
        <p><label >{message}</label></p>
        <button onClick={copyToClipboard}>
        {isCopied ? 'Copied' : 'Copy'}
      </button>
      </div>
      <footer>
  <a >Data processed solely within the context and are not stored or processed anywhere else. @PY</a>
</footer>
    </div>
  );
}

export default App;
